

import { Directive, Host, Optional } from '@angular/core';
import { TDSDatePickerComponent } from './date-picker.component';

@Directive({
  selector: 'tds-month-picker',
  exportAs: 'tdsMonthPicker'
})
// tslint:disable-next-line:directive-class-suffix
export class TDSMonthPickerComponent {
  constructor(@Optional() @Host() public datePicker: TDSDatePickerComponent) {
    this.datePicker.mode = 'month';
  }
}
