

import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { TDSHelperObject, TDSSafeAny } from 'tds-ui/shared/utility';
import { CandyDate } from 'tds-ui/core/time';
import { isNonEmptyString, isTemplateRef } from 'tds-ui/core/util';
import { DateHelperService, TDSCalendarI18nInterface } from 'tds-ui/i18n';
import { DatePickerService } from './date-picker.service';
import { transCompatFormat } from './lib/util';
import { CompatibleDate } from './standard-types';
import { PREFIX_CLASS } from './util';

@Component({
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  // tslint:disable-next-line:component-selector
  selector: 'calendar-footer',
  exportAs: 'calendarFooter',
  templateUrl:'./calendar-footer.component.html', 
})
export class CalendarFooterComponent implements OnChanges,  OnDestroy {
  @Input() locale!: TDSCalendarI18nInterface;
  @Input() showToday: boolean = false;
  @Input() showNow: boolean = false;
  @Input() hasTimePicker: boolean = false;
  @Input() isRange: boolean = false;
  @Input() listDate!: CompatibleDate | TDSSafeAny;

  @Input() okDisabled: boolean = false;
  @Input() disabledDate?: (d: Date) => boolean;
  @Input() extraFooter?: TemplateRef<void> | string;
  @Input() rangeQuickSelector: TemplateRef<any> | null = null;

  @Output() readonly clickOk = new EventEmitter<void>();
  @Output() readonly clickCancel = new EventEmitter<void>();
  @Output() readonly clickToday = new EventEmitter<CandyDate>();

  prefixCls: string = PREFIX_CLASS;
  isTemplateRef = isTemplateRef;
  isNonEmptyString = isNonEmptyString;
  isTodayDisabled: boolean = false;
  todayTitle: string = '';
  private destroy$ = new Subject<void>();
  startDate:string ="";
  endDate:string="";
  constructor(private dateHelper: DateHelperService,
    public datePickerService: DatePickerService,    
    ) {}
    
  ngOnChanges(changes: SimpleChanges): void {
    const now: Date = new Date();
    if (changes.disabledDate) {
      this.isTodayDisabled = !!(this.disabledDate && this.disabledDate(now));
    }
    if (changes.locale) {
      // NOTE: Compat for DatePipe formatting rules
      const dateFormat: string = transCompatFormat(this.locale.dateFormat);
      this.todayTitle = this.dateHelper.format(now, dateFormat);
    }
    if(changes.listDate)
    {
      if(this.isRange)
      {
        const dateFormat: string = transCompatFormat(this.locale.dateFormat);
       
        if(TDSHelperObject.hasValue(this.listDate))
        {
          let startDate = this.listDate[0];
          let endDate = this.listDate[1];
          if(TDSHelperObject.hasValue(startDate))
          {
            this.startDate =  this.dateHelper.format(startDate.nativeDate, dateFormat);
          }else{
            this.startDate ="";
          }
          if(TDSHelperObject.hasValue(endDate))
          {
            this.endDate =  this.dateHelper.format(endDate.nativeDate, dateFormat);
          }else{
            this.endDate ="";
          }
        }else{
          this.startDate ="";
          this.endDate ="";
        }
         
       
       
      }
    }
  }

  onClickToday(): void {
    const now: CandyDate = new CandyDate();
    this.clickToday.emit(now.clone()); // To prevent the "now" being modified from outside, we use clone
  }
  onClickCancel(): void {
    this.clickCancel.emit(); // To prevent the "now" being modified from outside, we use clone
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
 
}
