

import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DoCheck,
  ElementRef,
  EventEmitter,
  forwardRef,
  Host,
  Inject,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  Renderer2,
  Self,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, FormControl, FormGroupDirective, NgControl, NgForm, NG_VALUE_ACCESSOR } from '@angular/forms';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DatePickerService } from './date-picker.service';
import { DateRangePopupComponent } from './date-range-popup.component';
import { PREFIX_CLASS } from './util';

import { Direction, Directionality } from '@angular/cdk/bidi';
import { ESCAPE } from '@angular/cdk/keycodes';
import {
  CdkConnectedOverlay,
  CdkOverlayOrigin,
  ConnectedOverlayPositionChange,
  ConnectionPositionPair,
  HorizontalConnectionPos,
  VerticalConnectionPos
} from '@angular/cdk/overlay';
import { Platform } from '@angular/cdk/platform';
import { DOCUMENT } from '@angular/common';
import { CompatibleDate, DisabledTimeFn, TDSDateMode, PresetRanges, RangePartType, SupportTimeOptions } from './standard-types';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';
import { ButtonSize, TDSConfigService } from "tds-ui/core/config";
import { TDSFormField, TDSFormFieldControl, TDS_FORM_FIELD } from 'tds-ui/form-field';
import { slideMotion } from 'tds-ui/core/animation';
import { FunctionProp, InputBoolean, OnChangeType, OnTouchedType, TDSHelperArray, TDSHelperObject, toBoolean, valueFunctionProp } from 'tds-ui/shared/utility';
import { DateHelperService, TDSDatePickerI18nInterface, TDSDatePickerLangI18nInterface, TDSI18nService } from 'tds-ui/i18n';
import { CandyDate, cloneDate, CompatibleValue, wrongSortOrder } from 'tds-ui/core/time';
import { TDSResizeObserver } from 'tds-ui/core/resize-observers';
import { TDSIsErrorState } from 'tds-ui/shared/common';

const POPUP_STYLE_PATCH = { position: 'relative' }; // Aim to override antd's style to support overlay's position strategy (position:absolute will cause it not working because the overlay can't get the height/width of it's content)

export type TDSDatePickerSizeType = 'large' | 'default' | 'small';

/**
 * The base picker for all common APIs
 */
@Component({
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'tds-date-picker,tds-week-picker,tds-month-picker,tds-year-picker,tds-range-picker',
  exportAs: 'tdsDatePicker',
  templateUrl: './date-picker.component.html',
  host: {
    '[class.tds-picker]': `true`,
    '[class.tds-picker-range]': `isRange`,
    '[class.tds-picker-disabled]': `disabled`,
    '[class.tds-picker-rtl]': `dir === 'rtl'`,
    '[class.tds-picker-borderless]': `borderless`,
    '[class.tds-picker-inline]': `inline`,
    'class': 'w-full block relative box-border m-0 p-0',
    '(click)': 'onClickInputBox($event)',

  },
  providers: [
    DatePickerService,
    {
      provide: TDSFormFieldControl,
      useExisting: forwardRef(() => TDSDatePickerComponent)
    }
  ],
  animations: [slideMotion]
})
export class TDSDatePickerComponent implements TDSFormFieldControl<any>, OnInit, OnChanges, OnDestroy, DoCheck, AfterViewInit, ControlValueAccessor {
  static ngAcceptInputType_allowClear: BooleanInput;
  static ngAcceptInputType_autoFocus: BooleanInput;
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_borderless: BooleanInput;
  static ngAcceptInputType_inputReadOnly: BooleanInput;
  static ngAcceptInputType_inline: BooleanInput;
  static ngAcceptInputType_isOpen: BooleanInput;
  static ngAcceptInputType_showToday: BooleanInput;
  static ngAcceptInputType_showNow: BooleanInput;
  static ngAcceptInputType_mode: TDSDateMode | TDSDateMode[] | string | string[] | null | undefined;
  static ngAcceptInputType_showTime: BooleanInput | SupportTimeOptions | null | undefined;

  isRange: boolean = false; // Indicate whether the value is a range value
  extraFooter?: TemplateRef<any> | string;
  dir: Direction = 'ltr';

  public panelMode: TDSDateMode | TDSDateMode[] = 'date';
  private destroyed$: Subject<void> = new Subject();
  private isCustomPlaceHolder: boolean = false;
  private isCustomFormat: boolean = false;
  private _showTime: SupportTimeOptions | boolean = false;

  // --- Common API
  @Input() @InputBoolean() allowClear: boolean = true;
  @Input() @InputBoolean() autoFocus: boolean = false;
  @Input() @InputBoolean() disabled: boolean = false;
  @Input() @InputBoolean() borderless: boolean = false;
  @Input() @InputBoolean() inputReadOnly: boolean = false;
  @Input() @InputBoolean() inline: boolean = false;
  @Input() @InputBoolean() isOpen?: boolean;
  @Input() disabledDate?: (d: Date) => boolean;
  @Input() locale!: TDSDatePickerI18nInterface;
  @Input() placeholder: string | string[] = '';
  @Input() popupStyle: object = POPUP_STYLE_PATCH;
  @Input() dropdownClassName?: string;
  @Input() format!: string;
  @Input() dateRender?: TemplateRef<any> | string | FunctionProp<TemplateRef<Date> | string>;
  @Input() disabledTime?: DisabledTimeFn;
  @Input() renderExtraFooter?: TemplateRef<any> | string | FunctionProp<TemplateRef<any> | string>;
  @Input() @InputBoolean() showToday: boolean = true;
  @Input() mode: TDSDateMode = 'date';
  @Input() @InputBoolean() showNow: boolean = true;
  @Input() ranges?: PresetRanges;
  @Input() defaultPickerValue: CompatibleDate | null = null;
  @Input() deparator?: string = undefined;
  @Input() suffixIcon: string | TemplateRef<any> = 'tdsi-calendar-fill';
  @Input() backdrop = false;
  @Input() id: string | null = null;
  @Input() tdsClass: string = "";
  @Input()
  get size(): ButtonSize {
    return this._size;
  }
  set size(value: ButtonSize) {
    if (this._size != value) {
      this._size = value;
      this.stateChanges.next();
    }

  }
  private _size: ButtonSize = "md";
  // TODO(@wenqi73) The PanelMode need named for each pickers and export
  @Output() readonly panelChange = new EventEmitter<TDSDateMode | TDSDateMode[] | string | string[]>();
  @Output() readonly calendarChange = new EventEmitter<Array<Date | null>>();
  @Output() readonly ok = new EventEmitter<CompatibleDate | null>();
  @Output() readonly openChange = new EventEmitter<boolean>();

  @Input() get showTime(): SupportTimeOptions | boolean {
    return this._showTime;
  }

  set showTime(value: SupportTimeOptions | boolean) {
    this._showTime = typeof value === 'object' ? value : toBoolean(value);
  }

  // ------------------------------------------------------------------------
  // Input API Start
  // ------------------------------------------------------------------------
  @ViewChild(CdkConnectedOverlay, { static: false }) cdkConnectedOverlay?: CdkConnectedOverlay;
  @ViewChild(DateRangePopupComponent, { static: false }) panel!: DateRangePopupComponent;
  @ViewChild('separatorElement', { static: false }) separatorElement?: ElementRef;
  @ViewChild('pickerInput', { static: false }) pickerInput?: ElementRef<HTMLInputElement>;
  @ViewChildren('rangePickerInput') rangePickerInputs?: QueryList<ElementRef<HTMLInputElement>>;
  stateChanges = new Subject<void>();
  value: any;
  focused: boolean = false;
  get empty() {
    return false;
  }
  @Input()
  get required(): boolean {
    return this._required;
  }
  set required(value: boolean) {
    this._required = coerceBooleanProperty(value);
    this.stateChanges.next();
  }
  private _required = false;
  parentIsFormField: boolean = false;
  origin: CdkOverlayOrigin;
  document: Document;
  inputSize: number = 12;
  inputWidth?: number;
  private destroy$ = new Subject<void>();
  prefixCls = PREFIX_CLASS;
  inputValue!: any;
  activeBarStyle: object = {};
  overlayOpen: boolean = false; // Available when "isOpen" = undefined
  overlayPositions: ConnectionPositionPair[] = [
    {
      offsetY: 2,
      originX: 'start',
      originY: 'bottom',
      overlayX: 'start',
      overlayY: 'top'
    },
    {
      offsetY: -2,
      originX: 'start',
      originY: 'top',
      overlayX: 'start',
      overlayY: 'bottom'
    },
    {
      offsetY: 2,
      originX: 'end',
      originY: 'bottom',
      overlayX: 'end',
      overlayY: 'top'
    },
    {
      offsetY: -2,
      originX: 'end',
      originY: 'top',
      overlayX: 'end',
      overlayY: 'bottom'
    }
  ] as ConnectionPositionPair[];
  currentPositionX: HorizontalConnectionPos = 'start';
  currentPositionY: VerticalConnectionPos = 'bottom';
  currentPickerValue: CompatibleValue | null = null;
  tdsCssContainer: string = "";
  get realOpenState(): boolean {
    // The value that really decide the open state of overlay

    return this.isOpenHandledByUser() ? !!this.isOpen : this.overlayOpen;
  }

  ngAfterViewInit(): void {
    if (this.autoFocus) {
      this.focus();
    }

    if (this.isRange && this.platform.isBrowser) {
      this.resizeObserver
        .observe(this.elementRef)
        .pipe(takeUntil(this.destroy$))
        .subscribe(() => {
          this.updateInputWidthAndArrowLeft();
        });
    }

    this.datePickerService.inputPartChange$.pipe(takeUntil(this.destroy$)).subscribe(partType => {
      if (partType) {
        this.datePickerService.activeInput = partType;
      }
      this.focus();
      this.updateInputWidthAndArrowLeft();
    });
  }

  updateInputWidthAndArrowLeft(): void {
    this.inputWidth = this.rangePickerInputs?.first?.nativeElement.offsetWidth || 0;

    const baseStyle = { position: 'absolute', width: `${this.inputWidth}px` };
    this.datePickerService.arrowLeft = this.datePickerService.activeInput === 'left' ? 0 : this.inputWidth + this.separatorElement?.nativeElement.offsetWidth || 0;

    if (this.dir === 'rtl') {
      this.activeBarStyle = { ...baseStyle, right: `${this.datePickerService.arrowLeft}px` };
    } else {
      this.activeBarStyle = { ...baseStyle, left: `${this.datePickerService.arrowLeft}px` };
    }

    this.cdr.markForCheck();
  }

  getInput(partType?: RangePartType): HTMLInputElement | undefined {
    if (this.inline) {
      return undefined;
    }
    return this.isRange
      ? partType === 'left'
        ? this.rangePickerInputs?.first.nativeElement
        : this.rangePickerInputs?.last.nativeElement
      : this.pickerInput!.nativeElement;
  }

  focus(): void {
    const activeInputElement = this.getInput(this.datePickerService.activeInput);
    if (this.document.activeElement !== activeInputElement) {
      activeInputElement?.focus();
    }
    if (!this.focused && !this.disabled) {
      this.focused = true;
      this.stateChanges.next();
    }
  }

  onFocus(event: FocusEvent, partType?: RangePartType): void {
    event.preventDefault();
    if (partType) {
      this.datePickerService.inputPartChange$.next(partType);
    }
    this.renderClass(true);

  }

  // blur event has not the relatedTarget in IE11, use focusout instead.
  onFocusout(event: FocusEvent): void {
    event.preventDefault();
    this.renderClass(false);
  }

  // Show overlay content
  open(): void {
    if (this.isRange) {
      this.currentPickerValue = this.datePickerService.hasValue() ? this.datePickerService.value : null;
    }
    if (this.inline) {
      return;
    }
    if (!this.realOpenState && !this.disabled) {

      this.updateInputWidthAndArrowLeft();
      this.overlayOpen = true;
      this.openChange.emit(true);
      this.updateCssContainer();
      this.cdr.markForCheck();
    }
  }

  close(): void {
    if (this.inline || this.isRange) {
      return;
    } else {
      this.p_Close();
    }

  }
  private p_Close() {
    if (this.realOpenState) {
      this.overlayOpen = false;
      this.focused = false;
      this.stateChanges.next();
      this.openChange.emit(false);
      this.onTouchedFn();
      this.updateCssContainer();
    }
  }
  showClear(): boolean {
    return !this.disabled && !this.isEmptyValue(this.datePickerService.value) && this.allowClear;
  }

  checkAndClose(): void {

    if (!this.realOpenState) {
      return;
    }

    if (this.panel.isAllowed(this.datePickerService.value!, true)) {
      if (Array.isArray(this.datePickerService.value) && wrongSortOrder(this.datePickerService.value)) {
        const index = this.datePickerService.getActiveIndex();
        const value = this.datePickerService.value[index];
        this.panel.changeValueFromSelect(value!, true);
        return;
      }

      if (!this.isRange) {
        //cp nht modelchange khi ng popup
        if (this.showTime) {
          this.updateInputValue();
          this.datePickerService.emitValue$.next();
        }
        else
          this.close();
      }

    } else {
      this.datePickerService.setValue(this.datePickerService.initialValue!);
      this.close();
    }
  }

  onClickInputBox(event: MouseEvent): void {
    event.stopPropagation();
    this.focus();
    if (!this.isOpenHandledByUser()) {
      this.open();
    }
  }

  onOverlayKeydown(event: KeyboardEvent): void {
    if (event.keyCode === ESCAPE) {
      this.datePickerService.initValue();
    }
  }

  // NOTE: A issue here, the first time position change, the animation will not be triggered.
  // Because the overlay's "positionChange" event is emitted after the content's full shown up.
  // All other components like "tds-dropdown" which depends on overlay also has the same issue.
  // See: https://github.com/NG-ZORRO/ng-zorro-antd/issues/1429
  onPositionChange(position: ConnectedOverlayPositionChange): void {
    this.currentPositionX = position.connectionPair.originX;
    this.currentPositionY = position.connectionPair.originY;
    this.cdr.detectChanges(); // Take side-effects to position styles
  }

  onClickClear(event: MouseEvent): void {
    event.preventDefault();
    event.stopPropagation();

    this.datePickerService.initValue(true);
    this.datePickerService.emitValue$.next();
  }

  updateInputValue(): void {
    const newValue = this.datePickerService.value;
    if (this.isRange) {
      this.inputValue = newValue ? (newValue as CandyDate[]).map(v => this.formatValue(v)) : ['', ''];
    } else {
      this.inputValue = this.formatValue(newValue as CandyDate);
    }
    this.cdr.markForCheck();
  }

  formatValue(value: CandyDate): string {

    return this.dateHelper.format(value && (value as CandyDate).nativeDate, this.format);
  }

  onInputChange(value: string, isEnter: boolean = false): void {
    /**
     * in IE11 focus/blur will trigger ngModelChange if placeholder changes,
     * so we forbidden IE11 to open panel through input change
     */
    if (
      !this.platform.TRIDENT &&
      this.document.activeElement === this.getInput(this.datePickerService.activeInput) &&
      !this.realOpenState
    ) {
      this.open();
      return;
    }

    const date = this.checkValidDate(value);
    // Can only change date when it's open
    if (date && this.realOpenState) {
      this.panel.changeValueFromSelect(date, isEnter);
    }
  }

  onKeyupEnter(event: Event): void {
    this.onInputChange((event.target as HTMLInputElement).value, true);
  }

  private checkValidDate(value: string): CandyDate | null {
    const date = new CandyDate(this.dateHelper.parseDate(value, this.format));

    if (!date.isValid() || value !== this.dateHelper.format(date.nativeDate, this.format)) {
      return null;
    }

    return date;
  }

  getPlaceholder(partType?: RangePartType): string {
    return this.isRange ? this.placeholder[this.datePickerService.getActiveIndex(partType!)] : (this.placeholder as string);
  }

  isEmptyValue(value: CompatibleValue): boolean {
    if (value === null) {
      return true;
    } else if (this.isRange) {
      return !value || !Array.isArray(value) || value.every(val => !val);
    } else {
      return !value;
    }
  }

  // Whether open state is permanently controlled by user himself
  isOpenHandledByUser(): boolean {
    return this.isOpen !== undefined;
  }

  // ------------------------------------------------------------------------
  // Input API End
  // ------------------------------------------------------------------------

  constructor(
    public tdsConfigService: TDSConfigService,
    public datePickerService: DatePickerService,
    protected i18n: TDSI18nService,
    protected cdr: ChangeDetectorRef,
    private renderer: Renderer2,
    private elementRef: ElementRef,
    private dateHelper: DateHelperService,
    private resizeObserver: TDSResizeObserver,
    private platform: Platform,
    @Inject(DOCUMENT) doc: any,
    @Optional() private directionality: Directionality,
    @Optional() @Inject(TDS_FORM_FIELD) @Host() public _formField: TDSFormField,
    @Optional() @Self() public ngControl: NgControl,
    @Optional() public _parentForm: NgForm,
    @Optional() public _parentFormGroup: FormGroupDirective,
    // @Host() @Optional() public noAnimation?: false
  ) {
    this.document = doc;
    this.origin = new CdkOverlayOrigin(this.elementRef);
    if (this.ngControl != null) {
      this.ngControl.valueAccessor = this;
    }
    if (this._formField) {
      // this.size = this._formField.size;
      this.parentIsFormField = true;
    }
  }

  ngOnInit(): void {
    this.updateCssContainer();
    // Subscribe the every locale change if the locale is not handled by user
    if (!this.locale) {
      this.i18n.localeChange.pipe(takeUntil(this.destroyed$)).subscribe(() => this.setLocale());
    }
    // Default value
    this.datePickerService.isRange = this.isRange;
    this.datePickerService.initValue(true);
    this.datePickerService.emitValue$.pipe(takeUntil(this.destroyed$)).subscribe(_ => {

      const value = this.datePickerService.value;
      this.datePickerService.initialValue = cloneDate(value);
      if (this.isRange) {
        const vAsRange = value as CandyDate[];
        if (TDSHelperArray.hasListValue(vAsRange)) {
          this.onChangeFn([vAsRange[0]?.nativeDate ?? null, vAsRange[1]?.nativeDate ?? null]);
        } else {
          this.onChangeFn([]);
        }
      } else {
        if (value) {
          this.onChangeFn((value as CandyDate).nativeDate);
        } else {
          this.onChangeFn(null);
        }
      }
      this.onTouchedFn();
      // When value emitted, overlay will be closed
      this.close();
    });

    this.directionality.change?.pipe(takeUntil(this.destroyed$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });
    this.dir = this.directionality.value;
    this.inputValue = this.isRange ? ['', ''] : '';
    this.setModeAndFormat();

    this.datePickerService.valueChange$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.updateInputValue();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.popupStyle) {
      // Always assign the popup style patch
      this.popupStyle = this.popupStyle ? { ...this.popupStyle, ...POPUP_STYLE_PATCH } : POPUP_STYLE_PATCH;
    }

    // Mark as customized placeholder by user once placeholder assigned at the first time
    if (changes.placeholder?.currentValue) {
      this.isCustomPlaceHolder = true;
    }

    if (changes.format?.currentValue) {
      this.isCustomFormat = true;
    }

    if (changes.locale) {
      // The locale is currently handled by user
      this.setDefaultPlaceHolder();
    }

    if (changes.renderExtraFooter) {
      this.extraFooter = valueFunctionProp(this.renderExtraFooter!);
    }

    if (changes.mode) {
      this.setDefaultPlaceHolder();
      this.setModeAndFormat();
    }
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  setModeAndFormat(): void {
    const local = this.i18n.getLocale();
    let inputFormats: { [key in TDSDateMode]?: string } = {
      year: 'yyyy',
      month: 'yyyy/MM',
      week: this.i18n.getDateLocale() ? 'RRRR/II' : 'yyyy/ww', // Format for week
      date: this.showTime ? 'yyyy/MM/dd HH:mm:ss' : 'yyyy/MM/dd'
    };
    if (TDSHelperObject.hasValue(local)) {
      inputFormats = {
        year: local.DatePicker.lang.yearFormat ? local.DatePicker.lang.yearFormat : 'yyyy',
        month: local.DatePicker.lang.monthFormat ? local.DatePicker.lang.monthFormat : 'yyyy/MM',
        week: this.i18n.getDateLocale() ? 'RRRR/II' : 'ww', // Format for week
        date: this.showTime ? local.DatePicker.lang.dateTimeFormat : local.DatePicker.lang.dateFormat
      };
    }

    if (!this.mode) {
      this.mode = 'date';
    }

    this.panelMode = this.isRange ? [this.mode, this.mode] : this.mode;

    // Default format when it's empty
    if (!this.isCustomFormat) {
      this.format = inputFormats[this.mode as TDSDateMode]!;
    }
    this.inputSize = Math.max(10, this.format.length) + 2;
    this.updateInputValue();
  }

  /**
   * Triggered when overlayOpen changes (different with realOpenState)
   * @param open The overlayOpen in picker component
   */
  onOpenChange(open: boolean): void {
    this.openChange.emit(open);
  }

  // ------------------------------------------------------------------------
  // | Control value accessor implements
  // ------------------------------------------------------------------------

  // NOTE: onChangeFn/onTouchedFn will not be assigned if user not use as ngModel
  onChangeFn: OnChangeType = () => void 0;
  onTouchedFn: OnTouchedType = () => void 0;

  writeValue(value: CompatibleDate): void {
    //  li khng binding dc data khi dng formcontrol, do gi writevalue trc ngOnInit
    setTimeout(() => {
      this.setValue(value);
      this.cdr.markForCheck();
    }, 0);
  }

  registerOnChange(fn: OnChangeType): void {
    this.onChangeFn = fn;
  }

  registerOnTouched(fn: OnTouchedType): void {
    this.onTouchedFn = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.cdr.markForCheck();
  }

  // ------------------------------------------------------------------------
  // | Internal methods
  // ------------------------------------------------------------------------

  // Reload locale from i18n with side effects
  private setLocale(): void {
    this.locale = this.i18n.getLocaleData('DatePicker', {});
    this.setDefaultPlaceHolder();
    this.cdr.markForCheck();
  }

  private setDefaultPlaceHolder(): void {
    if (!this.isCustomPlaceHolder && this.locale) {
      const defaultPlaceholder: { [key in TDSDateMode]?: string } = {
        year: this.getPropertyOfLocale('yearPlaceholder'),
        month: this.getPropertyOfLocale('monthPlaceholder'),
        week: this.getPropertyOfLocale('weekPlaceholder'),
        date: this.getPropertyOfLocale('placeholder')
      };

      const defaultRangePlaceholder: { [key in TDSDateMode]?: string[] } = {
        year: this.getPropertyOfLocale('rangeYearPlaceholder'),
        month: this.getPropertyOfLocale('rangeMonthPlaceholder'),
        week: this.getPropertyOfLocale('rangeWeekPlaceholder'),
        date: this.getPropertyOfLocale('rangePlaceholder')
      };

      this.placeholder = this.isRange
        ? defaultRangePlaceholder[this.mode as TDSDateMode]!
        : defaultPlaceholder[this.mode as TDSDateMode]!;
    }
  }

  private getPropertyOfLocale<T extends keyof TDSDatePickerLangI18nInterface>(type: T): TDSDatePickerLangI18nInterface[T] {

    return this.locale.lang[type] || this.i18n.getLocaleData(`DatePicker.lang.${type}`);
  }

  // Safe way of setting value with default
  private setValue(value: CompatibleDate): void {
    const newValue: CompatibleValue = this.datePickerService.makeValue(value);
    this.datePickerService.setValue(newValue);
    this.datePickerService.initialValue = newValue;
  }

  renderClass(value: boolean): void {
    // TODO: avoid autoFocus cause change after checked error
    if (value) {
      this.renderer.addClass(this.elementRef.nativeElement, 'tds-picker-focused');
    } else {
      this.renderer.removeClass(this.elementRef.nativeElement, 'tds-picker-focused');
    }

  }

  onPanelModeChange(panelMode: TDSDateMode | TDSDateMode[]): void {
    this.panelChange.emit(panelMode);
  }

  // Emit calendarChange when select date by tds-range-picker
  _calendarChange(value: CompatibleValue): void {
    if (this.isRange && Array.isArray(value)) {
      const rangeValue = value.filter(x => x instanceof CandyDate).map(x => x!.nativeDate);
      this.calendarChange.emit(rangeValue);
    }
  }

  onResultOk(): void {
    if (this.isRange) {
      const value = this.datePickerService.value as CandyDate[];
      if (value.length) {
        this.ok.emit([value[0]?.nativeDate || null, value[1]?.nativeDate || null]);
      } else {
        this.ok.emit([]);
      }
      if (!this.showTime || this.panel.isAllowed(this.datePickerService.value!, true))
        this.p_Close();
    } else {
      if (this.datePickerService.value) {
        this.ok.emit((this.datePickerService.value as CandyDate).nativeDate);
      } else {
        this.ok.emit(null);
      }
    }
  }
  onResultCancel(): void {
    this.datePickerService.setValue(this.currentPickerValue);
    this.datePickerService.emitValue$.next();
    this.p_Close();
  }
  ngDoCheck() {

    if (this.ngControl) {
      this.updateErrorState();
    }
  }
  errorState: boolean = false;
  updateErrorState() {
    const oldState = this.errorState;
    const parent = this._parentFormGroup || this._parentForm;
    // const matcher = this.errorStateMatcher || this._defaultErrorStateMatcher;
    const control = this.ngControl ? this.ngControl.control as FormControl : null;
    const newState = this._isErrorState(control, parent);
    if (newState !== oldState) {
      this.errorState = newState;
      this.stateChanges.next();
      this.cdr.markForCheck();
    }
  }
  private _isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {

    return TDSIsErrorState(control, form);
  }
  // get errorState(): boolean {
  //   return this.ngControl && this.ngControl.errors !== null && !!this.ngControl.touched;
  // }
  setDescribedByIds(ids: string[]) {
    // if (ids.length) {
    //   this._elementRef.nativeElement.setAttribute('aria-describedby', ids.join(' '));
    // } else {
    //   this._elementRef.nativeElement.removeAttribute('aria-describedby');
    // }
  }
  onContainerClick(e: MouseEvent) {
    // e.preventDefault();
    // e.stopPropagation();

  }
  onClickOutside(e: MouseEvent) {
    e.preventDefault();
    e.stopPropagation();
    this.checkAndClose();
  }

  updateCssContainer() {
    this.tdsCssContainer = '';
    if (!this.parentIsFormField) {
      this.tdsCssContainer += 'border  rounded bg-white dark:bg-d-neutral-3-50';
      if (this.overlayOpen) {
        this.tdsCssContainer += ' outline-none ring-opacity-20 ring ring-primary-1 border-primary-1';
      } else {
        this.tdsCssContainer += ' border-neutral-2-200 dark:border-d-neutral-2-200'
      }
    }

    this.tdsCssContainer += ` ${this.tdsClass}`;
  }
}
